import { stagger20ms } from '../../../@vex/animations/stagger.animation';
import { fadeInUp400ms } from '../../../@vex/animations/fade-in-up.animation';
import { scaleFadeIn400ms } from '../../../@vex/animations/scale-fade-in.animation';
import icAdd from '@iconify/icons-ic/twotone-add';
import icMoreHoriz from '@iconify/icons-ic/twotone-more-horiz';
import { AttendanceDefinitionsComponent } from '../attendance/attendance-definitions.component';
import { MatDialog} from '@angular/material/dialog';
import { AttendanceService } from '../services/attendance.service';

import {
  MAT_MOMENT_DATE_FORMATS,
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS
} from "@angular/material-moment-adapter";
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE
} from "@angular/material/core";
import { Validators, FormControl } from '@angular/forms';



@Component({
  selector: 'vex-leave-record',
  templateUrl: './leave-record.component.html',
  styleUrls: ['./leave.component.scss'],
  animations: [
    stagger20ms,
    fadeInUp400ms,
    scaleFadeIn400ms
  ],
  providers: [
  
    {
      provide: MAT_DATE_FORMATS,
      useValue: {
        parse: {
          dateInput: ['l', 'LL'],
        },
        display: {
          dateInput: 'L',
          monthYearLabel: 'M YYYY',
          dateA11yLabel: 'LL',
          monthYearA11yLabel: 'MMMM YYYY',
        },
      },
    },
    { provide: DateAdapter, useClass: MomentDateAdapter },
    { provide: MAT_MOMENT_DATE_ADAPTER_OPTIONS, useValue: { useUtc: true } } 
  ]
})


export class LeaveRecordComponent implements OnInit {

  language: any = []; languageMap: LanguageMap = {};
  static id = 100;

  @Input() columns = ['ID', 'Code', 'Name','CardNo'];
  form: FormGroup;
  mode: 'create' | 'update' = 'create';
  dataSource: MatTableDataSource<any> | null;
  icMoreVert = icMoreVert;
  icClose = icClose;
  saveEnabled = false;
  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;
  icSave = icSave;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icDeleteForever=icDeleteForever;
  icPhone = icPhone;
  visible = false;
  label = '';
  icAdd= icAdd;
  icMoreHoriz=icMoreHoriz;
  InOutType = [{ Code: 'G', Name: 'Giri' }, { Code: 'C', Name: 'k' }];
  InOutReason : any[];
  // sub=false;

  constructor(@Inject(MAT_DIALOG_DATA) public defaults: any,
    private dialogRef: MatDialogRef<LeaveRecordComponent>,
    private fb: FormBuilder,
    private cd: ChangeDetectorRef,
    public definitionsService: DefinitionsService,
    private adapter: DateAdapter<any>,
    private dialog: MatDialog,
    public attendanceService: AttendanceService,
    private snackbar: MatSnackBar) {
  }
 
  refresh() {
    this.cd.detectChanges();

  }
 
 turkishCulture() {
    this.adapter.setLocale("tr");
  }

  
  getReasons(type) {

    this.definitionsService.DefinitionGetConstants(type)
      .subscribe((res: any[]) => {
        this.InOutReason=res;

      });
  }

  ngOnInit() {
    const locale = 'en-US';
    this.turkishCulture();
    this.languageMap = AppUtil.getLabels();  
    this.getReasons('defaults');
      if (this.defaults.Data==undefined)
    {this.form = this.fb.group({  Code:null, Name:null, StartDate:null, StartTime:'00:00', InFlag:null,InReason:null, 
      EndDate:null,EndTime:'00:00', OutFlag:null,OutReason:null, });}
      else
      {
        debugger;
        let neden:any;
        if (this.defaults.Data.NEDEN==0) neden=null; else neden=this.defaults.Data.NEDEN.toString();
      if (this.defaults.Data.GCKODU=='G')
      {
      
      this.form = this.fb.group({ID:this.defaults.Data.IDNO, Code:this.defaults.Data.PRSICIL, Name:this.defaults.Data.Name+' '+this.defaults.Data.Surname, StartDate:this.defaults.Data.TARIH, StartTime:formatDate(this.defaults.Data.ZAMAN, 'HH:mm', locale), InFlag:this.defaults.Data.GCKODU,InReason:neden ,EndDate:null, EndTime:'00:00', OutFlag:null,OutReason:null });
      }      
      else
      {
      this.form = this.fb.group({ ID:this.defaults.Data.IDNO, Code:this.defaults.Data.PRSICIL, Name:this.defaults.Data.Name+' '+this.defaults.Data.Surname, StartDate:null, StartTime:'00:00', InFlag:null,InReason:null, 
        EndDate:this.defaults.Data.TARIH,EndTime:formatDate(this.defaults.Data.ZAMAN,'HH:mm', locale), OutFlag:this.defaults.Data.GCKODU,OutReason:neden });
      }
    }
  
  }
  
  openDefinition(value) {
    this.dialog.open(AttendanceDefinitionsComponent, {
      data: { Type: value, Data: { ID: null, Code: null, Name: null } },
      disableClose: false,
      width: '400px',
      height: '700px'
    }).afterClosed().subscribe(result => {
 
      if (value=='0') {        
       this.form.patchValue({ Code: result.Code, Name:result.Name,Surname:result.Surname });
       
      }

  

    });
  }

  waitProgressDismiss() {
    this.snackbar.dismiss();
  }

  addRecord()
  {
    this.waitProgress(this.languageMap['lem yaplyor. Ltfen Bekleyiniz...'].labelName,undefined);
    const definition = this.form.value;
    definition.CompanyId=JSON.parse(sessionStorage.getItem('CompanyId'));
    definition.Username=JSON.parse(sessionStorage.getItem('Username'));
    // definition.ID=0;
    this.attendanceService.PostAttendance(definition)
    .subscribe((res) => {
    //  const row= this.dataSource.data.filter(x => x.id === obj.id);     
      // this.definitionGetTypesDistibution(obj.workingTypeID);
      this.waitProgressDismiss();
      this.saveEnabled = false;

    }, err => {
        console.log(err);
      this.waitProgress(err,3000); 
    });


  }


  waitProgress(message, timer) {
    this.snackbar.dismiss();
    let config = new MatSnackBarConfig();
    if (timer !== undefined) {
      config.duration = timer;
    }
    config.panelClass = ['message-position'];
    this.snackbar.open(message, "", config);

  }

  save() {
    if (this.mode === 'create') {
      this.createDefinition();
    } else if (this.mode === 'update') {
      this.updateDefinition();
    }
  }

  createDefinition() {
    const definition = this.form.value;

    if (this.defaults.modul === 'code' && (definition.code === '' || definition.name === '')) {
      this.waitProgress("Kod ve aklama alan zorunludur...", 1000);
      return;
    }


    if (!definition.imageSrc) {
      definition.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(definition);
  }

  updateDefinition() {
    const definition = this.form.value;
    if (this.defaults.sub && this.defaults.modul === 'code' && (definition.subcode === '' || definition.subname === '')) {
      this.waitProgress("Alt Kod ve aklama alan zorunludur...", 1000);
      return;
    }
    definition.id = this.defaults.def.id;
    console.log(definition);
    this.dialogRef.close(definition);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
